﻿import { Injectable } from '@angular/core';
import { HttpParams } from '@angular/common/http';
import { Location } from '@angular/common';
import { isObject, isArray, isDate, isFunction } from 'util';

@Injectable({
	providedIn: 'root'
})

export class UrlHelperService {
	private convertRec(o: any, qs: HttpParams, prefix: string = null): HttpParams {
		for (let p in o) {
			if (!o.hasOwnProperty(p))
				continue;
			let v = o[p];
			if (v == undefined || isFunction(v))
				continue;
			let name = (prefix ? prefix + "." : "") + p;
			if (isDate(v)) {
				v = v.toISOString();
			}
			if (isArray(v)) {
				for (let key in v) {
					if (!v.hasOwnProperty(key))
						continue;
					qs = qs.append(name, v[key]);
				}
				continue;
			} else if (isObject(v)) {
				qs = this.convertRec(v, qs, name);
				continue;
			}

			if (v != undefined && v !== "") {
				qs = qs.set(name, v);
			}
		}
		return qs;
	}

	public dataToParams(data: any, source: HttpParams = null) {
		return this.convertRec(data, source || new HttpParams());
	}

	public saveToUrl(data: any, location: Location) {
		let qs: HttpParams;
		if (data instanceof HttpParams) {
			qs = data;
		} else {
			qs = this.dataToParams(data);
		}
		var path = location.path().split('?')[0];
		location.replaceState(path, qs.toString());
	}
}