﻿import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { Location } from '@angular/common';
import { Observable } from 'rxjs';

import { EStarWarsDataType } from '../../services/api/api-models';
import { SearchStarWarsReqDto } from '../../services/api/api-models-classes';
import { StarwarsApiService } from '../../services/api/starwars-api-service';
import { UrlHelperService } from '../../services/url-helper.service';
import { UserProgressService } from '../../services/user-progress.service';

@Component({
	selector: 'app-page-search',
	templateUrl: './page-search.component.html',
	styleUrls: ['./page-search.component.less']
})
export class PageSearchComponent implements OnInit {
	EStarWarsDataType = EStarWarsDataType;
	criteria = new SearchStarWarsReqDto();
	searched = false;
	errMessage: string;
	data: any;

	constructor(
		private title: Title,
		private starwarsApiService: StarwarsApiService,
		private location: Location,
		private urlHelper: UrlHelperService,
		private progress: UserProgressService
	) {
	}

	ngOnInit() {
		this.title.setTitle('search_starwars');
	}

	isAnyCriteria() {
		if (this.criteria.StarWarsDataType) {
			if (this.criteria.Name && this.criteria.Name.length >= 2)
				return true;
			if (this.criteria.Title && this.criteria.Title.length >= 2)
				return true;
			if (this.criteria.Model && this.criteria.Model.length >= 2)
				return true;
		}
		return false;
	}

	onClear() {
		this.criteria = new SearchStarWarsReqDto();
		this.errMessage = null;
		this.searched = false;
		this.urlHelper.saveToUrl({}, this.location);
	}

	onSearch() {
		this.errMessage = null;

		if (!this.isAnyCriteria()) {
			this.errMessage = 'please_provide_at_least_one_criteria';
			return;
		}

		this.update();
	}

	onStarwarsDataTypeValueChange(dataType: EStarWarsDataType) {
		if (!dataType)
			return;

		switch (dataType) {
		case EStarWarsDataType.People:
			this.criteria.Title = this.criteria.Model = null;
			this.criteria.StarWarsDataType = EStarWarsDataType.People;
			break;
		case EStarWarsDataType.Films:
			this.criteria.Name = this.criteria.Model = null;
			this.criteria.StarWarsDataType = EStarWarsDataType.Films;
			break;
		case EStarWarsDataType.Planets:
			this.criteria.Title = this.criteria.Model = null;
			this.criteria.StarWarsDataType = EStarWarsDataType.Planets;
			break;
		case EStarWarsDataType.Species:
			this.criteria.Title = this.criteria.Model = null;
			this.criteria.StarWarsDataType = EStarWarsDataType.Species;
			break;
		case EStarWarsDataType.Starships:
			this.criteria.Title = null;
			this.criteria.StarWarsDataType = EStarWarsDataType.Starships;
			break;
		case EStarWarsDataType.Vehicles:
			this.criteria.Title = null;
			this.criteria.StarWarsDataType = EStarWarsDataType.Vehicles;
			break;
		}
	}

	private update() {
		this.urlHelper.saveToUrl(this.criteria, this.location);

		if (this.criteria.StarWarsDataType === EStarWarsDataType.People) {
			this.execSearch(this.starwarsApiService.pilotSearch(this.criteria));
		} else if (this.criteria.StarWarsDataType === EStarWarsDataType.Films) {
			this.execSearch(this.starwarsApiService.filmSearch(this.criteria));
		} else if (this.criteria.StarWarsDataType === EStarWarsDataType.Planets) {
			this.execSearch(this.starwarsApiService.planetSearch(this.criteria));
		} else if (this.criteria.StarWarsDataType === EStarWarsDataType.Species) {
			this.execSearch(this.starwarsApiService.specieSearch(this.criteria));
		} else if (this.criteria.StarWarsDataType === EStarWarsDataType.Starships) {
			this.execSearch(this.starwarsApiService.starshipSearch(this.criteria));
		} else if (this.criteria.StarWarsDataType === EStarWarsDataType.Vehicles) {
			this.execSearch(this.starwarsApiService.vehicleSearch(this.criteria));
		}
	}

	private execSearch<T>(obs: Observable<T>) {
		this.progress.runAsync(obs)
			.then(data => {
				this.data = data;
				this.searched = true;
			});
	}
}